import { Component, Vue } from 'vue-property-decorator'
import { rents } from '../../../api/report/reportApi'

@Component({
  components: {}
})
export default class occupancy extends Vue {
  private pageTitle = '入住率分析'
  private query = {
    endDate: '',
    startDate: ''
  }
  private dataSource: any[] = []

  public mounted(): void {
    this.getRents()
  }

  private async getRents() {
    const res = await rents(this.query)
    this.dataSource = res.data
  }
}
